.home__page__first__sections{
    @apply p-3
}

.home__section__one__content{
    @apply md:flex block w-full
}

.left__side__content{
    @apply md:w-96 border-t border-solid border-gray-900
}

.right__side__content{
    @apply md:ml-10;
}

.right__side__content .right__side__first__iage{
    background-image: url('../../assets/images/others/image__one.jpg');
    width: 700px;
    height: 330px;
    background-position: center;
    background-size: cover;
    position: relative;
}
@media(max-width: 600px) {
    .right__side__content .right__side__first__iage{
        width: 100%!important;
    }
}
.text__wrapper{
    @apply absolute bottom-0 pl-6 pb-3
}

.right__side__first__iage__two{
    background-image: linear-gradient(to bottom, rgba(0,0,0,0.1), rgba(0,0,0,0.3)), url('../../assets/images/others/image_two.jpg');
