@import url('https://fonts.googleapis.com/css2?family=Inter+Tight:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,200;1,300;1,400;1,500&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,200;1,300;1,400&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;1,100;1,300;1,400;1,500&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,200;1,300;1,400;1,500&display=swap');
.main__header__air{
    @apply bg-white z-10
}

.navigation{
    background-image: url('../assets/images/others/banner.jpg');
    @apply bg-cover bg-no-repeat bg-center;
    min-height: 80vh;
}

.nav{
    @apply bg-white w-full h-14 flex items-center justify-between pl-3 pr-3
}

/* sideba__menu */
.sideba__menu{
    @apply fixed top-0 right-0 left-0 w-full bottom-0 z-10 overflow-hidden min-h-screen;
    background-color: rgba(0,0,0,0.6);
}

.sideba__menu .side__bar__navigation{
    @apply w-80 bg-white min-h-screen rounded-t-md relative
}

.right__side__list__anchor{
    @apply md:flex hidden items-center space-x-6
}

/* content__main__banner */
.content__main__banner{
    @apply flex justify-center
}

.banner__content{
    @apply flex flex-col pt-28 w-full relative
}

.banner__content a {
    @apply md:text-6xl text-white font-thin text-xl text-center mb-4
}
.banner__content .buttons__tabs button{
    background-color: rgba(0,0,0,0.4);
    @apply text-white md:px-10 md:py-3 mt-6 border-none md:text-lg text-xs px-2 py-2 outline-none mr-1;
}

.first__tab__main{
    background-color: rgba(0,0,0,0.6);
    width: 934px!important;
    @apply md:w-full flex-1 grow p-3;
    
}

.first__tab__main input{
    @apply md:w-96 md:ml-2 outline-none border-none h-12 text-xl mb-2 md:mb-0 font-thin placeholder-black pl-3
}

.first__tab__main button{
    @apply bg-red-600 md:w-52 h-12 w-full md:ml-2 md:mt-0 text-white
}

.active__btn{
    background-color: rgba(0,0,0,0.6)!important;
}

@media(max-width: 600px) {
    .first__tab__main{
        width: 300px!important;
    }
}
/* login__popup */
.login__popup{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(255,255,255,.7);
    min-height: 100vh;
    width: 100%;
    max-width: 100%;
    z-index: 100;
    @apply flex justify-center items-center
}

.login__account__box{
    width: 450px;
}